.navigation {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 5vh;
  width: 100vw;
}

nav {
  background: transparent;
  -webkit-box-shadow: none;
          box-shadow: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

nav a {
  font-family: 'Work Sans', sans-serif;
  font-weight: lighter;
  font-size: 1em;
  text-transform: uppercase;
  margin: 0 2em;
  color: rgba(255, 255, 255, 0.7);
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

nav a:hover {
  color: white;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}



